import React, {Component} from 'react';
import Navbar from './Navbar';
import Card from './Card';
import './App.css';
import loadinggif from './loading-buffering.gif';

let workspaceData = [];
let workspacesToShow = [];

class App extends Component {
  constructor(props) {
     super(props);
     this.state = {data : null, showdata : null};
     this.updateData = this.updateData.bind(this);
     this.filterData = this.filterData.bind(this);
  }
  componentDidMount() {
    Promise.all([
	   fetch('https://api.airtable.com/v0/app6xReKkFDtzQ655/MATHia%20Workspaces?maxRecords=100&view=1-100&api_key=keyuFtFTBZMHrzjOn'),
	   fetch(`https://api.airtable.com/v0/app6xReKkFDtzQ655/MATHia%20Workspaces?maxRecords=100&view=101-200&api_key=keyuFtFTBZMHrzjOn`),
     fetch(`https://api.airtable.com/v0/app6xReKkFDtzQ655/MATHia%20Workspaces?maxRecords=100&view=201-300&api_key=keyuFtFTBZMHrzjOn`),
     fetch(`https://api.airtable.com/v0/app6xReKkFDtzQ655/MATHia%20Workspaces?maxRecords=100&view=301-400&api_key=keyuFtFTBZMHrzjOn`),
     fetch(`https://api.airtable.com/v0/app6xReKkFDtzQ655/MATHia%20Workspaces?maxRecords=100&view=401-500&api_key=keyuFtFTBZMHrzjOn`),
     fetch(`https://api.airtable.com/v0/app6xReKkFDtzQ655/MATHia%20Workspaces?maxRecords=100&view=501-600&api_key=keyuFtFTBZMHrzjOn`),
     fetch(`https://api.airtable.com/v0/app6xReKkFDtzQ655/MATHia%20Workspaces?maxRecords=100&view=601-700&api_key=keyuFtFTBZMHrzjOn`)])
    .then(function (responses) {
	     return Promise.all(responses.map(function (response) {
		   return response.json();
	   }));
    }).then(function (data) {
      	workspaceData.push(...data[0].records, ...data[1].records, ...data[2].records, ...data[3].records,
                           ...data[4].records, ...data[5].records, ...data[6].records);
      });
    var that = this;
    setTimeout(function(){that.updateData();}, 2000);
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (this.state.showdata !== nextState.showdata) return true;
  }
  updateData() {
    this.setState({data : workspaceData, showdata : workspaceData});
    workspaceData = [];
  }
  filterData(query) {
    workspacesToShow = [];
    workspaceData = this.state.data.filter(function(item) {
      return item.fields.Workspace.toLowerCase().match(query.toLowerCase()) || item.fields.Description.toLowerCase().match(query.toLowerCase()) || item.fields.CCSS.includes(query) || item.fields['Course(s) and Sequences'].includes(query) || item.fields['Workspace ID'].toLowerCase() === query.toLowerCase();
    });
    this.setState({showdata : workspaceData});
  }
  render() {
    if (!this.state.data) return <img className='loading-gif' src={loadinggif} alt='loading gif' />
    else {
    for (var a = 0; a < this.state.showdata.length; a++) {
    var newDescription = this.state.showdata[a].fields.Description.length > 125 ? this.state.showdata[a].fields.Description.slice(0, 125) + 'â€¦' : this.state.showdata[a].fields.Description;
    workspacesToShow.push(<Card workspace={this.state.showdata[a].fields.Workspace} description={newDescription} fulldescription={this.state.showdata[a].fields.Description} ccss={this.state.showdata[a].fields.CCSS} time={this.state.showdata[a].fields['Median Time']} sampleimg={this.state.showdata[a].fields.SampleImg} sbs={this.state.showdata[a].fields.SBS} sequence={this.state.showdata[a].fields['Course(s) and Sequences']} wksptype={this.state.showdata[a].fields['CT/NM']} wkspid={this.state.showdata[a].fields['Workspace ID']} key={a} />);
    }
    return (
    <>
    <Navbar filterdata={this.filterData} datalen={this.state.showdata.length} />
    <div className="App">
    {workspacesToShow}
    </div>
    </>
    );
    }
  }
}

export default App;
