import React, {Component} from 'react';
import placeholderpic from './picture_icon.png';
import Carousel from 'react-bootstrap/Carousel';
import Tippy from '@tippyjs/react';
import 'tippy.js/dist/tippy.css';

class Card extends Component {
  constructor(props) {
    super(props);
    this.state = {time : null, sampleimg : null, sequence : null};
  }
  componentDidMount() {
    this.setState({index : 0});
  }
  handleSelect = (selectedIndex, e) => {
    var inx = this.state.index;
    inx += 1;
    inx = inx % 2;
    this.setState({index : inx});
  }
  render = () => {
    var backgroundStyle = '#6667AB';
    /*if (this.state.sequence.includes('Course 1')) backgroundStyle = 'rgba(0, 132, 127, 0.5)';
    else if (this.state.sequence.includes('Course 2')) backgroundStyle = 'rgba(0, 125, 182, 0.5)';
    else if (this.state.sequence.includes('Course 3')) backgroundStyle = 'rgba(133, 40, 108, 0.5)';
    else if (this.state.sequence.includes('Algebra I')) backgroundStyle = 'rgba(0, 132, 127, 0.9)';
    else if (this.state.sequence.includes('Geometry')) backgroundStyle = 'rgba(0, 125, 182, 0.9)';
    else if (this.state.sequence.includes('Algebra II')) backgroundStyle = 'rgba(133, 40, 108, 0.9)';
    else if (this.state.sequence.includes('Pima Community College')) backgroundStyle = 'rgba(0, 82, 155, 0.9)';
    else if (this.state.sequence.includes('MS RtI')) backgroundStyle = 'rgba(0, 132, 127, 0.3)';
    else backgroundStyle = 'lightgray';*/
    return (
    <div className='card col-sm-12'>
    <h5 className="card-header text-start" style={{backgroundColor : backgroundStyle, color : 'white'}}>{this.props.wksptype === 'NM\n' ? <Tippy content='Concept Builder Workspace'><i className="bi bi-bricks"></i></Tippy> : <Tippy content='Mastery Workspace'><i className="bi bi-star"></i></Tippy>}</h5>

    <Carousel activeIndex={this.state.index} onSelect={this.handleSelect} interval={null}>
    <Carousel.Item>
    <img className="d-block w-100" src={this.props.sampleimg ? this.props.sampleimg[0].url : placeholderpic} alt='preview of workspace' />
    </Carousel.Item>
    <Carousel.Item>
    <img className="d-block w-100" src={this.props.sbs ? this.props.sbs[0].url : placeholderpic} alt='step by step' />
    </Carousel.Item>
    </Carousel>

    <div className='card-body d-flex flex-column'>
    <a href='https://www.carnegielearning.com'><h5 className='card-title'>{this.props.workspace}</h5></a>
    <p className='card-text text-start text-justify'>{this.props.description}</p>
    {this.props.ccss ? <div className="card-footer text-muted mt-auto text-start"><i className="bi bi-list-check"></i> {this.props.ccss.join(', ')}&nbsp;&nbsp;&nbsp;&nbsp;<i className="bi bi-hourglass-split"></i> {this.props.time / 60} min. <Tippy content={<span style={{fontSize: 12}}>{this.props.wkspid}</span>}><i className="bi bi-bullseye" style={{position:'absolute', right : 25}}></i></Tippy></div> : null}
    </div>
    </div>
    );
  }
}

export default Card;
