import React, {Component} from 'react';

class Navbar extends Component {
   constructor(props) {
      super(props);
      this.state = {};
      this.handleSubmit = this.handleSubmit.bind(this);
      this.openNav = this.openNav.bind(this);
      this.closeNav = this.closeNav.bind(this);
      this.clearSearch = this.clearSearch.bind(this);
   }
   handleSubmit() {
      let query = document.getElementById('searchbox').value;
      this.props.filterdata(query);
   }
   clearSearch() {
      let query = '';
      document.getElementById('searchbox').value = '';
      this.props.filterdata(query);
   }
   openNav() {
      document.getElementById("search-sidebar").style.width = "250px";
      document.getElementById("main").style.marginLeft = "250px";
   }
   closeNav() {
      document.getElementById("search-sidebar").style.width = "0";
      document.getElementById("main").style.marginLeft = "0";
   }
   render() {
      return (
      <>
      <div id='search-sidebar' className='sidebar'>
      <a href='#' className='closebtn' onClick={this.closeNav}>&times;</a>
      <input className="form-control mr-sm-2" id='searchbox' type="search" placeholder="Search" aria-label="Search" style={{width : '90%', marginTop : 5, marginLeft : 10, marginBottom : 10}} />
      <button className="btn btn-primary my-2 my-sm-0" type="submit" onClick={this.handleSubmit} style={{marginLeft : 10}}>Search</button>
      <button className="btn btn-outline-light my-2 my-sm-0" type="submit" onClick={this.clearSearch} style={{marginLeft : 5}}>Clear</button>
      <p style={{marginLeft : 10, color : 'white', position : 'absolute', bottom : 15}}>Showing <span style={{color : 'blue'}}>{this.props.datalen}</span> workspaces.</p>
      </div>
      <div id='main'>
      <button className='openbtn' onClick={this.openNav}>&#9776;</button>
      </div>
      </>
      );
   }
}

export default Navbar;
